<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="addIsVisible"
      :show-close="false"
      fullscreen
    >
      <!--  -->
      <el-form :model="addForm" class="Form" >
        <!--  -->
        <div>
          <div></div>
          <el-form-item label="">
            <el-input v-model="addForm.studentName"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addForm.studentNum"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="addForm.studentClass">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </div>
        <!--  -->
        <div>
          <div></div>
          <el-form-item
            :label="item.key"
            v-for="(item, index) in Form.ProfessionalRequired"
            :key="index"
          >
            <el-input-number v-model="addForm[item.value]" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>
          </el-form-item>
        </div>
        <!--  -->
        <div>
          <div></div>
          <el-form-item
            :label="item.key"
            v-for="(item, index) in Form.PublicCompulsory"
            :key="index"
          >
            <el-input-number v-model="addForm[item.value]" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>
          </el-form-item>
        </div>
        <!--  -->
        <div>
          <div></div>
          <el-form-item
            :label="item.key"
            v-for="(item, index) in Form.ProfessionalPractice"
            :key="index"
          >
            <el-input-number v-model="addForm[item.value]" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>
          </el-form-item>
        </div>
        <!--  -->
        <div>
          <div></div>
          <el-form-item
            :label="item.key"
            v-for="(item, index) in Form.Generalknowledge"
            :key="index"
          >
            <el-input-number v-model="addForm[item.value]" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>
          </el-form-item>
        </div>
        <!--  -->
        <div></div>
        <div>
          <el-form-item
            :label="item.key"
            v-for="(item, index) in Form.physicalTest"
            :key="index"
          >
            <el-input-number v-model="addForm[item.value]" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>
          </el-form-item>
        </div>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeDialog"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { result } from "@/helper/utils";
export default {
  data() {
    return {
      addForm: {
        studentName: "",
        studentNum: "",
        studentClass: "",
      },
      Form: {},
    };
  },
  props: ["addIsVisible"],
  mounted() {
    this.getForm();
  },
  methods: {
    // 
    closeDialog() {
      // 
      this.$emit("addClose");
      // 
      this.addForm = {
        studentName: "",
        studentNum: "",
        studentClass: "",
      };
    },
    // 
    async submit() {
      // 
      this.$emit("addClose");
      // 
      const { data: res } = await this.$http.post("students/add", this.addForm);
      // 
      result(res)
      // 
      if(res.meta.code === 0) {
        return
      }
      // 
      this.addForm = {
        studentName: "",
        studentNum: "",
        studentClass: "",
      };
      this.$emit("getStudents")
    },
    // 
    async getForm() {
      const { data: res } = await this.$http.get("/form");
      this.Form = res.data;
    },
  },
};
</script>

<style lang="scss" scoped>
.Form {
  div {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    .el-form-item {
      width: 170px;
      margin: 0 5px 10px;
    }
  }
}
