<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="updateIsVisible"
      :show-close="false"
      fullscreen
    >
      <!--  -->
          <el-form :model="updateForm" class="Form" >
        <!--  -->
        <div>
          <div></div>
          <el-form-item label="">
            <el-input v-model="updateForm.studentName"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="updateForm.studentNum"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="updateForm.studentClass">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </div>
        <!--  -->
        <div>
          <div></div>
          <el-form-item
            :label="item.key"
            v-for="(item, index) in Form.ProfessionalRequired"
            :key="index"
          >
            <el-input-number v-model="updateForm[item.value]" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>
          </el-form-item>
        </div>
        <!--  -->
        <div>
          <div></div>
          <el-form-item
            :label="item.key"
            v-for="(item, index) in Form.PublicCompulsory"
            :key="index"
          >
            <el-input-number v-model="updateForm[item.value]" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>
          </el-form-item>
        </div>
        <!--  -->
        <div>
          <div></div>
          <el-form-item
            :label="item.key"
            v-for="(item, index) in Form.ProfessionalPractice"
            :key="index"
          >
            <el-input-number v-model="updateForm[item.value]" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>
          </el-form-item>
        </div>
        <!--  -->
        <div>
          <div></div>
          <el-form-item
            :label="item.key"
            v-for="(item, index) in Form.Generalknowledge"
            :key="index"
          >
            <el-input-number v-model="updateForm[item.value]" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>
          </el-form-item>
        </div>
        <!--  -->
        <div></div>
        <div>
          <el-form-item
            :label="item.key"
            v-for="(item, index) in Form.physicalTest"
            :key="index"
          >
            <el-input-number v-model="updateForm[item.value]" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>
          </el-form-item>
        </div>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeDialog"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { result } from "@/helper/utils";
export default {
  data() {
    return {
      updateForm: {},
      Form: {},
      newid: "",
    };
  },
  props: ["updateIsVisible", "id"],
  mounted() {
    this.getForm();
  },
  watch: {
    // id, idwatchidid
    async id(newVal) {
      const { data: res } = await this.$http.get("students/" + newVal);
      // 
      const {
        basicInfo,
        Generalknowledge,
        ProfessionalPractice,
        ProfessionalRequired,
        PublicCompulsory,
        physicalTest,
      } = res.data;
      Generalknowledge.forEach((item) => {
        delete item.courseName;
        delete item.courseValue;
      });
      const a = Object.assign({}, ...Generalknowledge);
      ProfessionalPractice.forEach((item) => {
        delete item.courseName;
        delete item.courseValue;
      });
      const b = Object.assign({}, ...ProfessionalPractice);
      ProfessionalRequired.forEach((item) => {
        delete item.courseName;
        delete item.courseValue;
      });
      const c = Object.assign({}, ...ProfessionalRequired);
      PublicCompulsory.forEach((item) => {
        delete item.courseName;
        delete item.courseValue;
      });
      const d = Object.assign({}, ...PublicCompulsory);
      physicalTest.forEach((item) => {
        delete item.courseName;
        delete item.courseValue;
      });
      const e = Object.assign({}, ...physicalTest);
      this.updateForm = Object.assign({}, a, b, c, d, e, basicInfo);
      this.updateForm.id = newVal;
    },
  },
  methods: {
    // 
    closeDialog() {
      // 
      this.$emit("updateClose");
    },
    // 
    async getForm() {
      const { data: res } = await this.$http.get("/form");
      this.Form = res.data;
    },
    // 
    async submit() {
      // 
      const { data: res } = await this.$http.post("students/update", this.updateForm);
      result(res);
      // 
      this.$emit("updateClose");
      this.$emit("getStudents")
    },
  },
};
</script>

<style lang="scss" scoped>
.Form {
  div {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    .el-form-item {
      width: 170px;
     margin: 0 5px 10px;
    }
  }
}
